'use client'

import React, { useState, useEffect, useMemo } from 'react'
import { Check, Zap, Globe, Cpu, Hash, PenTool } from 'lucide-react'

interface SkillNode {
    id: string
    label: string
    category: 'AI' | 'Dev' | 'Hard' | 'Math' | 'Soft'
    level: 1 | 2 | 3 | 4 // 1: 核心, 4: 边缘/前沿
    angle: number // 角度 (0-360)
}

const SKILL_NODES: SkillNode[] = [
    // --- L1: 理论基石 (内环) ---
    { id: 'math', label: '离散数学', category: 'Math', level: 1, angle: 0 },
    { id: 'algo', label: '算法基础', category: 'Math', level: 1, angle: 72 },
    { id: 'logic', label: '数理逻辑', category: 'Math', level: 1, angle: 144 },
    { id: 'stats', label: '概率统计', category: 'Math', level: 1, angle: 216 },
    { id: 'theory', label: '计算理论', category: 'Math', level: 1, angle: 288 },

    // --- L2: 通用工具 (次内环) ---
    { id: 'python', label: 'Python', category: 'AI', level: 2, angle: 10 },
    { id: 'cpp', label: 'C++', category: 'Hard', level: 2, angle: 82 },
    { id: 'ts', label: 'TypeScript', category: 'Dev', level: 2, angle: 160 },
    { id: 'linux', label: 'Linux/OS', category: 'Dev', level: 2, angle: 230 },
    { id: 'git', label: '版本控制', category: 'Soft', level: 2, angle: 300 },

    // --- L3: 技术栈 (中外环) ---
    { id: 'ml', label: '机器学习', category: 'AI', level: 3, angle: 20 },
    { id: 'dl', label: '深度学习', category: 'AI', level: 3, angle: 45 },
    { id: 'react', label: 'React.js', category: 'Dev', level: 3, angle: 170 },
    { id: 'node', label: 'Node.js', category: 'Dev', level: 3, angle: 190 },
    { id: 'embedded', label: '嵌入式系统', category: 'Hard', level: 3, angle: 90 },
    { id: 'fpga', label: '可编程逻辑', category: 'Hard', level: 3, angle: 110 },
    { id: 'research', label: '科研方法', category: 'Soft', level: 3, angle: 310 },
    { id: 'latex', label: 'LaTeX 排版', category: 'Soft', level: 3, angle: 330 },

    // --- L4: 专业前沿 (外环) ---
    { id: 'llm', label: '大语言模型', category: 'AI', level: 4, angle: 30 },
    { id: 'cv', label: '视觉识别', category: 'AI', level: 4, angle: 55 },
    { id: 'nlp', label: '语义语义', category: 'AI', level: 4, angle: 80 },
    { id: 'k8s', label: '云原生架构', category: 'Dev', level: 4, angle: 200 },
    { id: 'dist', label: '分布式存储', category: 'Dev', level: 4, angle: 220 },
    { id: 'ros', label: '机器人内核', category: 'Hard', level: 4, angle: 100 },
    { id: 'cuda', label: 'GPU 并行计算', category: 'Hard', level: 4, angle: 125 },
    { id: 'writing', label: '顶级论文写作', category: 'Soft', level: 4, angle: 345 },
]

const CATEGORY_META = {
    AI: { color: '#6366f1', icon: <Zap size={14} /> },
    Dev: { color: '#06b6d4', icon: <Globe size={14} /> },
    Hard: { color: '#10b981', icon: <Cpu size={14} /> },
    Math: { color: '#f59e0b', icon: <Hash size={14} /> },
    Soft: { color: '#8b5cf6', icon: <PenTool size={14} /> },
}

interface Props {
    selectedSkills: string[]
    onToggle: (id: string) => void
}

export default function MeshSkillSelector({ selectedSkills, onToggle }: Props) {
    const [isMounted, setIsMounted] = useState(false)

    useEffect(() => {
        setIsMounted(true)
    }, [])

    const getCoords = (angle: number, level: number) => {
        const radius = 60 + level * 55 // 根据层级确定半径
        const radian = (angle - 90) * (Math.PI / 180)
        return {
            x: 50 + (radius / 3.5) * Math.cos(radian),
            y: 50 + (radius / 2.5) * Math.sin(radian)
        }
    }

    if (!isMounted) return <div className="mesh-container glass" style={{ width: '100%', height: '600px', background: '#000' }} />

    return (
        <div className="mesh-container glass holo-radar-bg" style={{
            width: '100%',
            height: '650px',
            position: 'relative',
            overflow: 'hidden',
            borderRadius: '24px',
            border: '1px solid rgba(99, 102, 241, 0.2)'
        }}>
            {/* 背景雷达环 */}
            {[1, 2, 3, 4].map(l => (
                <div key={l} style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    width: `${(60 + l * 55) / 1.75}%`,
                    height: `${(60 + l * 55) / 1.25}%`,
                    border: '1px solid rgba(255,255,255,0.05)',
                    borderRadius: '50%',
                    transform: 'translate(-50%, -50%)',
                    pointerEvents: 'none'
                }} />
            ))}

            {/* 动态扫描线 */}
            <div style={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: '100%',
                height: '100%',
                background: 'conic-gradient(from 0deg, rgba(99, 102, 241, 0.1), transparent 60deg)',
                borderRadius: '50%',
                animation: 'radar-scan 6s linear infinite',
                zIndex: 1,
                pointerEvents: 'none'
            }} />

            {/* 核心中央枢纽 */}
            <div style={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                width: '40px',
                height: '40px',
                background: 'var(--primary)',
                borderRadius: '10px',
                transform: 'translate(-50%, -50%) rotate(45deg)',
                boxShadow: '0 0 30px var(--primary)',
                zIndex: 10,
                animation: 'core-pulse 3s ease-in-out infinite'
            }}>
                <div style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%) rotate(-45deg)', color: 'white' }}>
                    <Zap size={20} fill="white" />
                </div>
            </div>

            {/* 全息节点 */}
            {SKILL_NODES.map(node => {
                const { x, y } = getCoords(node.angle, node.level)
                const isSelected = selectedSkills.includes(node.id)
                const meta = CATEGORY_META[node.category]

                return (
                    <div
                        key={node.id}
                        onClick={() => onToggle(node.id)}
                        className="holo-node"
                        style={{
                            position: 'absolute',
                            left: `${x}%`,
                            top: `${y}%`,
                            transform: 'translate(-50%, -50%)',
                            cursor: 'pointer',
                            zIndex: 5,
                            transition: 'all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
                        }}
                    >
                        <div style={{
                            width: '45px',
                            height: '45px',
                            borderRadius: '12px',
                            background: isSelected ? meta.color : 'rgba(255,255,255,0.03)',
                            border: `1px solid ${isSelected ? 'white' : 'rgba(255,255,255,0.1)'}`,
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            justifyContent: 'center',
                            gap: '2px',
                            color: isSelected ? 'white' : '#94a3b8',
                            boxShadow: isSelected ? `0 0 15px ${meta.color}` : 'none',
                            backdropFilter: 'blur(5px)'
                        }}>
                            {isSelected ? <Check size={16} strokeWidth={3} /> : meta.icon}
                            <span style={{ fontSize: '10px', zoom: 0.8 }}>LV.{node.level}</span>
                        </div>

                        {/* 文字标签 - 极简排版 */}
                        <div style={{
                            position: 'absolute',
                            top: '120%',
                            left: '50%',
                            transform: 'translateX(-50%)',
                            whiteSpace: 'nowrap',
                            fontSize: '0.7rem',
                            fontWeight: isSelected ? 800 : 500,
                            color: isSelected ? 'white' : '#cbd5e1',
                            textShadow: isSelected ? `0 0 5px ${meta.color}` : 'none'
                        }}>
                            {node.label}
                        </div>

                        {/* 连接到核心的流光线 - 仅在选中时显示 */}
                        {isSelected && (
                            <svg style={{ position: 'absolute', inset: 0, width: '400px', height: '400px', transform: 'translate(-50%, -50%)', pointerEvents: 'none', zIndex: -1 }}>
                                <line
                                    x1="200" y1="200"
                                    x2={`${200 - (x - 50) * 4}`} y2={`${200 - (y - 50) * 4}`}
                                    stroke={meta.color}
                                    strokeWidth="1"
                                    opacity="0.3"
                                />
                            </svg>
                        )}
                    </div>
                )
            })}

            {/* 图例索引 */}
            <div style={{ position: 'absolute', bottom: '20px', left: '25px', display: 'flex', gap: '1rem', zIndex: 10 }}>
                {Object.entries(CATEGORY_META).map(([key, meta]) => (
                    <div key={key} style={{ display: 'flex', alignItems: 'center', gap: '0.4rem', fontSize: '0.7rem', color: '#94a3b8' }}>
                        <div style={{ width: '8px', height: '8px', borderRadius: '2px', background: meta.color }}></div>
                        {key}
                    </div>
                ))}
            </div>

            <div style={{ position: 'absolute', top: '20px', right: '25px', textAlign: 'right', color: '#6366f1', fontSize: '0.75rem', fontWeight: 'bold', zIndex: 10 }}>
                CRITICAL MATRIX v4.0.1
                <br />
                <span style={{ color: '#94a3b8', fontWeight: 'normal', fontSize: '10px' }}>HOLOGRAPHIC DATA SYSTEM</span>
            </div>
        </div>
    )
}
