'use client'

import { useState } from 'react'
import Navbar from '@/components/Navbar'
import { User, BookOpen, PenTool, Save, Plus, X, School, GraduationCap } from 'lucide-react'
import MeshSkillSelector from '@/components/MeshSkillSelector'

export default function ProfilePage() {
    const [role, setRole] = useState<'student' | 'teacher'>('student')
    const [selectedSkills, setSelectedSkills] = useState<string[]>(['python', 'algo'])

    const toggleSkill = (id: string) => {
        setSelectedSkills(prev =>
            prev.includes(id) ? prev.filter(s => s !== id) : [...prev, id]
        )
    }

    return (
        <main>
            <Navbar />

            <section className="section container" style={{ paddingTop: '140px' }}>
                <div style={{ maxWidth: '800px', margin: '0 auto' }}>
                    <div className="glass" style={{ padding: '2rem', marginBottom: '2rem', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <div style={{ display: 'flex', alignItems: 'center', gap: '1.5rem' }}>
                            <div style={{ width: '80px', height: '80px', background: 'var(--primary)', borderRadius: '20px', display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'white' }}>
                                <User size={40} />
                            </div>
                            <div>
                                <h2 style={{ marginBottom: '0.25rem' }}>个人中心</h2>
                                <p>设置您的个人资料并向潜在合作伙伴展示您的能力。</p>
                            </div>
                        </div>
                        <div style={{ display: 'flex', background: 'rgba(255,255,255,0.05)', padding: '0.4rem', borderRadius: '12px', border: '1px solid var(--glass-border)' }}>
                            <button
                                onClick={() => setRole('student')}
                                style={{
                                    padding: '0.6rem 1.2rem',
                                    borderRadius: '10px',
                                    border: 'none',
                                    cursor: 'pointer',
                                    background: role === 'student' ? 'var(--primary)' : 'transparent',
                                    color: 'white',
                                    fontWeight: 600,
                                    transition: 'all 0.2s'
                                }}
                            >
                                我是学生
                            </button>
                            <button
                                onClick={() => setRole('teacher')}
                                style={{
                                    padding: '0.6rem 1.2rem',
                                    borderRadius: '10px',
                                    border: 'none',
                                    cursor: 'pointer',
                                    background: role === 'teacher' ? 'var(--primary)' : 'transparent',
                                    color: 'white',
                                    fontWeight: 600,
                                    transition: 'all 0.2s'
                                }}
                            >
                                我是老师
                            </button>
                        </div>
                    </div>

                    <div className="grid" style={{ gap: '2rem' }}>
                        {/* 基本信息 */}
                        <div className="glass" style={{ padding: '2.5rem' }}>
                            <h3 style={{ marginBottom: '2rem', display: 'flex', alignItems: 'center', gap: '0.75rem' }}>
                                <PenTool size={20} color="var(--primary)" /> 基本信息
                            </h3>
                            <div className="grid" style={{ gridTemplateColumns: '1fr 1fr', gap: '1.5rem' }}>
                                <div>
                                    <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>姓名</label>
                                    <input type="text" className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none' }} placeholder="输入您的姓名" />
                                </div>
                                <div>
                                    <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>学院 / 部门</label>
                                    <input type="text" className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none' }} placeholder="例如：计算机学院" />
                                </div>
                                <div>
                                    <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>所在校区</label>
                                    <select className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none', background: 'var(--background)' }}>
                                        <option>本部校区</option>
                                        <option>沙河校区</option>
                                        <option>其他校区</option>
                                    </select>
                                </div>
                                <div>
                                    <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>联系方式 (Email)</label>
                                    <input type="email" className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none' }} placeholder="example@edu.cn" />
                                </div>
                            </div>
                        </div>

                        {/* 学术/课题信息 */}
                        <div className="glass" style={{ padding: '2.5rem' }}>
                            <h3 style={{ marginBottom: '2rem', display: 'flex', alignItems: 'center', gap: '0.75rem' }}>
                                {role === 'student' ? <GraduationCap size={20} color="var(--secondary)" /> : <School size={20} color="var(--secondary)" />}
                                {role === 'student' ? '学术背景' : '研究方向'}
                            </h3>

                            {role === 'student' && (
                                <div className="grid" style={{ gridTemplateColumns: '1fr 1fr', gap: '1.5rem', marginBottom: '1.5rem' }}>
                                    <div>
                                        <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>当前年级</label>
                                        <select className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none', background: 'var(--background)' }}>
                                            <option>本科大一</option>
                                            <option>本科大二</option>
                                            <option>本科大三</option>
                                            <option>本科大四</option>
                                            <option>硕士研究生</option>
                                            <option>博士研究生</option>
                                        </select>
                                    </div>
                                    <div>
                                        <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>GPA / 排名</label>
                                        <input type="text" className="glass" style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none' }} placeholder="如: 3.9/4.0 或 前5%" />
                                    </div>
                                </div>
                            )}

                            <div style={{ marginBottom: '1.5rem' }}>
                                <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>
                                    {role === 'student' ? '个人简介 / 项目经历' : '课题组简介 / 研究项目'}
                                </label>
                                <textarea
                                    className="glass"
                                    rows={4}
                                    style={{ width: '100%', padding: '0.8rem', color: 'white', border: '1px solid var(--glass-border)', outline: 'none', resize: 'vertical' }}
                                    placeholder={role === 'student' ? '介绍您的科研兴趣、参与过的竞赛或实验室轮转经历...' : '描述您的主要研究领域、在研课题以及对学生的要求...'}
                                />
                            </div>

                            <div>
                                <label style={{ display: 'block', marginBottom: '1.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>
                                    {role === 'student' ? '交互式技能树 (Neural Mesh)' : '研究领域分布'}
                                </label>

                                {role === 'student' ? (
                                    <MeshSkillSelector selectedSkills={selectedSkills} onToggle={toggleSkill} />
                                ) : (
                                    <div className="glass" style={{ padding: '3rem', textAlign: 'center', color: '#64748b', borderStyle: 'dashed' }}>
                                        <School size={48} style={{ marginBottom: '1rem', opacity: 0.5 }} />
                                        <p>研究方向分布图正在根据您的在研课题自动生成...</p>
                                    </div>
                                )}
                            </div>
                        </div>

                        <div style={{ textAlign: 'right' }}>
                            <button className="btn btn-primary" style={{ padding: '1rem 3rem' }}>
                                <Save size={20} /> 保存个人资料
                            </button>
                        </div>
                    </div>
                </div>
            </section>
        </main>
    )
}
